@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    margin: 0;
    background-image: url(/react-app/static/media/bg-desktop.6824779f.svg);
    background-color: hsl(257, 40%, 49%);
    background-size: contain;
    background-repeat: no-repeat;
    font-family: 'Open Sans', sans-serif;
}

header, main, footer {
    margin: auto 5em;
}

header {
    padding-top: 2.5em;
}

header img {
    width: 15%;
}

main {
    padding-top: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    /* grid-template-columns: 1.5fr 1fr; */
}

.main {
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.main .mockup {
    -webkit-flex: 3 1;
            flex: 3 1;
}


.writeup {
    padding: 1.5em 4em;
    color: white;
}

.writeup h1 {
    font-family: 'Poppins', sans-serif;
    line-height: 1.6em;
    padding-right: 5em;
}

.writeup p {
    padding-top: .5em;
    padding-right: 3.5em;
    line-height: 1.6em;
}

button {
    border-radius: 50px;
    color:  hsl(257, 40%, 49%);
    width: 150px;
    height: 50px;
    padding: .8em;
    font-size: 1em;
}

button:active, button:hover {
    background-color: hsl(300, 69%, 71%);
    color: white;
}

footer {
    padding: 1.5em 9em;
}

.icon {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.icon div {
    margin-left: 2em;
}

@media screen and (max-width: 380px) {
    header, main, footer {
        margin: auto 0;
    }

    header img {
        width: 50%;
        padding-left: 1.5em;
    }

    main {
        padding: 3em 1em;
        padding-bottom: 0;
        display: block;
        grid-template-columns: 1.5fr 1fr;
    }

    .mockup img {
        width: 90%;
        padding-left: 1.2em;
    }

    .writeup {
        text-align: center;
    }

    .writeup h1 {
        font-family: 'Poppins', sans-serif;
        line-height: 1.5em;
        padding: 0 1.5em;
        font-size: 1.5em;
    }

    .writeup p {
        line-height: 1.5em;
        padding: 1em 1em;

    }
    
    button {
        border-radius: 50px;
        color:  hsl(257, 40%, 49%);
        width: 60%;
        height: 40px;
        padding: .8em;
        font-size: .8em;
    }

    footer {
        padding-bottom: 1em;
    }

    .icon {
        -webkit-justify-content: center;
                justify-content: center;
        
    }
    .icon div {
        margin-left: .5em;
    }
}


